<template>
  <div class="bill-details">
    <div class="top-card">
      <p class="top">支付金额（元）</p>
      <p class="money">
        <span class="num">{{payAmount | numToMoney}}</span>
      </p>
    </div>
    <ul class="form">
      <li class="flex flex-between">
        <div class="title">支付方式</div>
        <div class="content">{{bankName}}({{cardNo}})</div>
      </li>
    </ul>
    <cube-button class="btn-main re-btn" @click="pay" :primary="true">确认支付</cube-button>
    <model v-if="dialog" title="支付验证" v-model="dialog" @confirmEvent="payConfirm">
      <div class="cube-input">
        <input v-model="phoneCode" type="text" placeholder="请输入验证码" class="cube-input-field">
        <cube-button
          class="code-btn"
          @click="sendCode"
          :disabled="codeBtn"
          :primary="true"
        >{{codeBtn ? `再次发送(${codeTime})` : '发送验证码'}}</cube-button>
      </div>
    </model>
  </div>
</template>

<script>
import { fetchHeader } from '../api/axios.js'
import Model from '../components/dialog'
import { brigeNativeByJavascript } from '../api/Bridge.js'
export default {
    data() {
        return {
            dialog: false,
            codeTime: 60,
            codeBtn: false,
            payAmount: '',
            id: '',
            cardNo: '',
            bankId: '',
            bankName: '',
            phoneCode: ''
        }
    },
    components: {
        Model
    },
    methods: {
        pay() {
            this.dialog = true
        },
        async sendCode() {
            this.verCodeTime()
            this.$store.commit('loading', true)
            const data = await fetchHeader('/bill/beginPromptRepay', { cardId: this.bankId, id: this.id }, 'post')
            if (data.code === 0) {
                this.$store.commit('setMsg', '发送成功')
            }
        },
        verCodeTime() {
            this.codeBtn = true
            const time = () => {
                setTimeout(() => {
                    if (this.codeTime === 0) {
                        this.codeBtn = false
                        this.codeTime = 60
                    } else {
                        this.codeTime -= 1
                        time()
                    }
                }, 1000)
            }
            time()
        },
        async getData() {
            const data = await fetchHeader('/auth/card/cardList', {}, 'post')
            this.cardNo = data.data[0].cardNo
            this.bankName = data.data[0].bankName
            this.bankId = data.data[0].id
            const data1 = await fetchHeader('/bill/billDetail', { id: this.id }, 'post')
            this.payAmount = data1.data.repayAmount
        },
        async payConfirm() {
            this.$store.commit('loading', true)
            const data = await fetchHeader(
                '/bill/payRepay',
                { id: this.id, cardId: this.bankId, phoneCode: this.phoneCode },
                'post'
            )
            if (data.code === 0) {
                brigeNativeByJavascript('closeApplication')
            }
        }
    },
    mounted() {
        this.id = this.$route.query.id
        document.title = '账单详情'
        this.getData()
    }
}
</script>

