<template>
<div class="cube-popup cube-popup_mask cube-dialog" style="z-index: 100;">
    <div class="cube-popup-mask"></div>
    <div class="cube-popup-container cube-popup-center">
        <div class="cube-popup-content">
            <div class="cube-dialog-main">
                <span class="cube-dialog-close" style="display: none;"><i class="cubeic-close"></i></span>
                <div class="cube-dialog-prompt">
                    <h2 v-if="hasTitle" class="cube-dialog-title">
                        <p class="cube-dialog-title-def">{{title}}</p>
                    </h2>
                    <div class="cube-dialog-content">
                        <div class="cube-dialog-content-def">
                            <slot></slot>
                        </div>
                    </div>
                    <div class="cube-dialog-btns border-right-1px">
                        <a @click="cancel" href="javascript:;" class="cube-dialog-btn border-top-1px">{{cancelText}}</a>
                        <a @click="sure" href="javascript:;" class="cube-dialog-btn border-top-1px cube-dialog-btn_highlight">{{confirmText}}</a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
    props: {
        title: {
            type: String,
            default: '标题'
        },
        hasTitle: {
            type: Boolean,
            default: true
        },
        value: {
            type: Boolean,
            default: false
        },
        confirmText: {
            type: String,
            default: '确认'
        },
        cancelText: {
            type: String,
            default: '取消'
        }
    },
    data() {
        return {
            val: !!this.value
        }
    },
    methods: {
        cancel() {
            this.val = false
            this.$emit('cancelEvent')
        },
        sure() {
            this.$emit('confirmEvent')
        }
    },
    watch: {
        val(n) {
            if (!n) {
                this.$emit('input', false)
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.cube-dialog-title-def {
    margin: 0.4rem 0.426667rem 0.4rem;
}
</style>